import React, {Component} from "react";
import {connect} from "react-redux";
import {
    propositionListCountIncrement,
    propositionListCountSet,
    requiredMsisdnVerificationOn,
    setPropositionListSoftBundleGroup
} from "eshop/modules/configurator/actions/filters";
import {getCartEntries} from "eshop/modules/cart/selectors";
import {addToCartWithCounter} from 'eshop/modules/configurator/actions/cart';
import {
    getContractRole,
    getFirstOfferFromProductFilter,
    getSelectedBaseRatePlanId,
    getSelectedOfferId
} from 'eshop/modules/configurator/selectors/offers';
import {
    getCheckMsisdnResult,
    getMarketContext,
    getMaxSimCount,
    getOfferTypeCmsConfMarket,
    getPropositionListCount,
    getPropositionListCountSetMode,
    getPropositionListSoftBundleGroup,
    getSelectedLoyaltyLengthValue,
    getSelectedOfferType,
    getSelectedProcessTypeValue,
    getSimCountSelected,
    isMsisdndVerificationRequired,
} from "eshop/modules/configurator/selectors/filters"
import PickDevicePopup from "eshop/modules/configurator/components/lp/offers/PickDevicePopup";
import OnlineUtils from "eshop/utils/OnlineUtils";
import {getCarouselReady, getOffersLoading} from 'eshop/modules/configurator/selectors/metaData';
import {gotoCartSummary} from "eshop/modules/checkout/actions/app"
import {clearAddTerminalToOfferFromSessionStorage} from "eshop/modules/cart/actions/cart";
import {setOfferType} from "eshop/modules/simfree/actions/app";
import {pickDevice, selectOffer} from 'eshop/modules/configurator/actions/offers';
import SimCartIndexHeader from "../../../cart/components/entriesList/mobile/SimCartIndexHeader"
import MarketSegment from "eshop/modules/core/enum/MarketSegment";
import AddMoreB2B from "eshop/modules/configurator/components/AddMoreB2B";
import AddMoreB2C from "eshop/modules/configurator/components/AddMoreB2C";
import {showError} from "eshop/modules/auth/actions/authorization"
import {getSelectedSoftBundleGroup} from "eshop/modules/simfree/selectors";

class AddToCartOrChooseTerminalComponent extends Component {

    constructor(props) {
        super(props);
        this.selectOfferNoPhoneAction = this.selectOfferNoPhoneAction.bind(this)
        this.pickDeviceAction = this.pickDeviceAction.bind(this)
        this.showPickDeviceModal = this.showPickDeviceModal.bind(this)
    }

    componentWillMount() {
        PickDevicePopup.initialize(
            OnlineUtils.extractObject(this.props.descriptions, "label.popup.")
        )
        this.props.clearAddTerminalToOfferFromSessionStorage()
    }
    componentWillReceiveProps(nextProps, nextContext) {
        console.log("AddToCartOrChooseTerminalComponent.componentWillReceiveProps: ", nextProps.propositionListSoftBundleGroup, nextProps.selectedSoftBundleGroup);
        console.log("this.props:",this.props);
        console.log("nxtprops:",nextProps);
        if(this.props.propositionListCount<1 &&  nextProps.selectedSoftBundleGroup && nextProps.propositionListSoftBundleGroup && nextProps.propositionListSoftBundleGroup !== nextProps.selectedSoftBundleGroup){
          console.log("AddToCartOrChooseTerminalComponent.componentWillReceiveProps XXXX")
           this.props.setPropositionListCount(1);
        }
    }
    selectOfferNoPhoneAction(e) {

        this.props.setPropositionListSoftBundleGroup();
        e && e.preventDefault()
        this.props.addToCartWithCounter()
    }

    pickDeviceAction(e) {
        console.log("pickDeviceAction")

        this.props.setPropositionListSoftBundleGroup();
        e && e.preventDefault()
        this.props.pickDevice(this.props.selectedOfferId, this.props.descriptions.deviceListAddress+'?');
    }

    showPickDeviceModal(e) {
        e.preventDefault()
        PickDevicePopup.open({
            onClickConfirm: this.pickDeviceAction,
            onClickDecline: this.selectOfferNoPhoneAction
        })
    }

    onClickAdd() {
        if (this.props.cartIsFix) {
            let msg = (this.props.descriptions && this.props.descriptions.fixincartmsg) || "W koszyku jest już oferta stacjonarna"
            this.props.showError(msg)
        } else if (this.props.propositionListCount > 0 && this.props.isMsisdndVerificationRequired && this.props.checkMsisdnResult && !this.props.checkMsisdnResult.isPositive && this.props.channels.sales != "WWW") {
            this.props.warningOn();
            OnlineUtils.scrollToComponent("msisdn-verification-container-row", "header");
        } else {

         //   this.props.setPropositionListSoftBundleGroup();
            this.props.selectOfferNoPhone(this.props.selectedOfferId);
        }
    }


    addMorePrice() {
        if (this.props.firstOfferFromProductFilter && this.props.firstOfferFromProductFilter.payments) {
            let priceObj = OnlineUtils.getPaymentsForRole(this.props.firstOfferFromProductFilter.payments, this.props.contractRole)
            return OnlineUtils.formatPrice(priceObj.basePrice.originalNetPrice)
        } else {
            return ""
        }
    }

    noScroll(event) {
        event.preventDefault();
    }

    increment() {
        this.props.increment();
        this.props.setOfferType(this.props.selectedOfferType);//to do zmienić na defaultowe

    }

    isAssignment(){
        return OnlineUtils.isAssignment(this.props.processType);
    }


    render() {

        if (this.props.isPropositionListCountSetMode || OnlineUtils.isAssignment(this.props.processType))
            return (null)
        let simCountQueue = [];
        let cartElementsCount = this.props.entries ? this.props.entries.length : 0;
        const {stepDescriptionLabel, priceDescriptionLabel} = {...this.props.descriptions};
        if(!this.props.propositionListSoftBundleGroup || this.props.propositionListSoftBundleGroup === this.props.selectedSoftBundleGroup){
            for (let i = cartElementsCount+1; i < this.props.simCountSelected; i++) {
                simCountQueue.push(<SimCountQueueElement key={"simQueueElement" + i} index={i}
                                                         {...{stepDescriptionLabel, priceDescriptionLabel}}/>);

            }
        }

        const addMore = this.props.isB2B ?
            <AddMoreB2B onClick={this.increment.bind(this)} price={this.addMorePrice()}/> :
            <AddMoreB2C onClick={this.increment.bind(this)}/>

        let isButtonEnabled = this.props.propositionListCount > 0 && (this.props.selectedLoyaltyLengthValue || this.props.selectedLoyaltyLengthValue === 0);

        return (
            <div className="l-full-row g-gray1-bg">
                {isButtonEnabled && !OnlineUtils.isMnpApplication(this.props.processType) &&
                <div className="l-page-row u-padding-xl u-padding-top-l ">
                    <div className=" u-border-top u-margin-left-s u-margin-right-s u-padding-m"></div>
                    <h4 className="h3 u-small-text-center u-small-hide">{this.props.descriptions.header}</h4>
                    <h4 className="h2 u-small-text-center u-padding-m u-large-hide u-medium-hide">{this.props.descriptions.header}</h4>


                    <ul className="l-row u-text-center">
                        <li className="l-col l-col-small-12 l-col-medium-6 l-col-6 ">
                            <div className="u-position-relative">

                                <button onClick={this.pickDeviceAction.bind(this)}
                                        disabled={!this.props.addToCartButtonEnabled}
                                        className=" o-btn opl-btn opl-btn--secondary u-w-100 u-no-border u-padding-all-l u-box-shadow u-box-shadow--s u-small-margin-l g-white1-bg">
                                    <p className={"h5 u-no-margin " + (!this.props.addToCartButtonEnabled && "g-gray5-c")}>{this.props.descriptions.labelAddWithTerminal || "Dobierz urządzenie"}</p>
                                </button>
                            </div>
                        </li>
                        <li className="l-col l-col-small-12 l-col-medium-6 l-col-6 ">
                            <div className="u-position-relative">
                                <button onClick={this.onClickAdd.bind(this)}
                                        disabled={!this.props.addToCartButtonEnabled}
                                        className=" o-btn opl-btn opl-btn--secondary u-w-100 u-no-border u-padding-all-l u-box-shadow u-box-shadow--s u-small-margin-l g-white1-bg">
                                    <p className={"h5 u-no-margin " + (!this.props.addToCartButtonEnabled && "g-gray5-c")}>{this.props.descriptions.labelAdd || "Wybierz bez urządzenia"}</p>
                                </button>
                            </div>
                        </li>
                    </ul>
                </div>
                }
                {isButtonEnabled && OnlineUtils.isMnpApplication(this.props.processType) &&
                    <div className="l-page-row u-padding-xl">
                        <div className="l-col l-col-small-12 l-col-medium-6">
                            <div className="u-position-relative">
                                <button onClick={this.onClickAdd.bind(this)}
                                        disabled={this.props.isMsisdndVerificationRequired && this.props.checkMsisdnResult && !this.props.checkMsisdnResult.isPositive}
                                        className=" o-btn opl-btn opl-btn--secondary u-w-100 u-no-border u-padding-all-l u-box-shadow u-box-shadow--s u-small-margin-l g-white1-bg">
                                    <p className={"h5 u-no-margin " + (this.props.isMsisdndVerificationRequired && this.props.checkMsisdnResult && !this.props.checkMsisdnResult.isPositive && "g-gray5-c")}>{this.props.descriptions.labelAddMnp || "Złóż wniosek na przeniesienie numeru"}</p>
                                </button>
                            </div>
                        </div>
                    </div>
                }

                {simCountQueue}
                {cartElementsCount < this.props.maxSimCount && (this.props.propositionListCount === 0) && addMore}
                {this.props.propositionListCount == 0 &&
                <GoToCart {...this.props}/>}
            </div>)

    }
}

class GoToCart extends Component {
    constructor(props) {
        super(props);
    }

    goToCart(e) {
        e.preventDefault();
        this.props.gotoCartSummary();
    }


    render() {
        return (
            <div className="l-page-row u-padding-l-xl u-padding-top-l">
                <div className="l-row">
                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-3  medium-offset-by-6 large-offset-by-9">
                        <button className="opl-btn opl-btn--primary opl-btn--medium o-btn u-w-100"
                                onClick={this.goToCart.bind(this)}
                                disabled={!this.props.addToCartButtonEnabled && this.props.propositionListCount > 0}>
                            {"Przejdź do koszyka"}
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}

